<template>
  <div>
    <div class="settings-container">
      <h3><i class="el-icon-setting"></i> Settings</h3>
      <el-row style="margin-top: 20px;">
        <el-tabs type="border-card" @tab-click="handleTabClick">
          <el-tab-pane>
            <span slot="label"><i class="iconfont icon-general"></i> General</span>
            <el-form ref="envForm" :model="env" label-width="120px">
              <el-form-item label="Workspace">
                <el-input v-model="env.envCode" disabled></el-input>
              </el-form-item>
              <el-form-item label="Description">
                <el-input v-model="env.envDesc"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" size="small" @click="saveEnv" :loading="buttonLoading">Save</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane>
            <span slot="label"><i class="iconfont icon-people"></i> People</span>
            <el-form :inline="true">
              <el-form-item>
                <el-input
                  placeholder="Find a member..."
                  v-model="findMember"
                  style="width: 300px;" clearable>
                  <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
              </el-form-item>
              <el-form-item style="float: right;">
                <el-button type="primary" size="small" @click="showAddMemberDialog">Add member</el-button>
              </el-form-item>
            </el-form>
            <el-table
              :data="foundMembers"
              style="width: 100%"
              max-height="450">
              <el-table-column
                label="Name">
                <template slot-scope="scope">
                  <div class="user-avator">
                    <img :src="scope.row.img">
                    <span style="margin-left: 10px">{{ scope.row.name }}</span>
                  </div>
                </template>
              </el-table-column>
              <el-table-column
                label="Email"
                width="300">
                <template slot-scope="scope">
                  {{ scope.row.email }}
                </template>
              </el-table-column>
              <el-table-column
                label="Role"
                width="150">
                <template slot-scope="scope">
                  <el-dropdown trigger="click" @command="handleChangeRole">
                    <span class="el-dropdown-link">                      
                      {{ scope.row.role }}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item :command="{email:scope.row.email,role:'Admin'}">Admin</el-dropdown-item>
                      <el-dropdown-item :command="{email:scope.row.email,role:'Developer'}">Developer</el-dropdown-item>
                      <el-dropdown-item :command="{email:scope.row.email,role:'Viewer'}">Viewer</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </template>
              </el-table-column>
              <el-table-column
                label="Status"
                width="150">
                <template slot-scope="scope">
                  <el-dropdown trigger="click" @command="handleChangeStatus">
                    <span class="el-dropdown-link">                      
                      {{ scope.row.status }}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item :command="{email:scope.row.email,status:'Approved'}">Approved</el-dropdown-item>
                      <el-dropdown-item :command="{email:scope.row.email,status:'Canceled'}">Canceled</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane>
            <span slot="label"><i class="iconfont icon-default"></i> Defaults</span>
            <el-form ref="defaultsForm" :model="defaults" label-width="120px">
              <el-form-item label="Batch User">
                <el-input v-model="defaults.user"></el-input>
              </el-form-item>
              <el-form-item label="JCL Lib">
                <el-input v-model="defaults.jclLib"></el-input>
              </el-form-item>
              <el-form-item label="DB2 Schema">
                <el-input v-model="defaults.db2Schema"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" size="small" @click="saveDefaults" :loading="buttonLoading">Save</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>

          <!-- feature -->
          <el-tab-pane>
            <span slot="label"><i class="iconfont icon-default"></i> Feature</span>
            <el-form :inline="true">
              <el-form-item>
                <el-input
                  placeholder="Find a feature..."
                  v-model="findFeature"
                  style="width: 300px;" clearable>
                  <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
              </el-form-item>
              <el-form-item style="float: right;">
                <el-button type="primary" size="small" @click="showAddFeatureDialog">Add Feature</el-button>
              </el-form-item>
            </el-form>
            <el-table
              :data="foundFeatures"
              style="width: 100%"
              max-height="300">
              <el-table-column
                label="Feature">
                <template slot-scope="scope">
                  {{ scope.row.featureName }}
                </template>
              </el-table-column>
              <el-table-column
                label="Description"
                width="450">
                <template slot-scope="scope">
                  {{ scope.row.featureDesc }}
                </template>
              </el-table-column>
              <el-table-column
                label="Status"
                width="150">
                <template slot-scope="scope">
                  <el-dropdown trigger="click" @command="handleChangeFeatureStatus">
                    <span class="el-dropdown-link">
                      {{ scope.row.status }}<i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                      <el-dropdown-item :command="{featureName:scope.row.featureName,status:'Approved'}">Approved
                      </el-dropdown-item>
                      <el-dropdown-item :command="{featureName:scope.row.featureName,status:'Canceled'}">Canceled
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <!-- feature -->

        </el-tabs>
      </el-row>
    </div>
    <!-- Add Member Dialog -->
    <el-dialog title="Add member" :visible.sync="addMemberDialogVisible" :close-on-click-modal="false"
               :show-close="true">
      <el-form :model="addMemberForm">
        <el-form-item label="Email" label-width="120px">
          <el-input v-model="addMemberForm.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="Role" label-width="120px">
          <el-select v-model="addMemberForm.role">
            <el-option label="Admin" value="Admin"></el-option>
            <el-option label="Developer" value="Developer"></el-option>
            <el-option label="Viewer" value="Viewer"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addMember" :loading="buttonLoading">Add</el-button>
      </div>
    </el-dialog>

    <!-- Add Feature Dialog -->
    <el-dialog title="Add member" :visible.sync="addFeatureDialogVisible" :close-on-click-modal="false"
               :show-close="true">
      <el-form :model="addFeatureForm">
        <el-row>
          <el-col :span="10">
            <el-form-item label="Feature:" label-width="120px">
              <el-input v-model="addFeatureForm.featureName" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="15">
            <el-form-item label="Description:" label-width="120px">
              <el-input v-model="addFeatureForm.featureDesc" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addFeature" :loading="buttonLoading">Add</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {
        env: {
          envCode: sessionStorage.getItem("workspace"),
          envDesc: ""
        },
        findMember: '',
        foundMembers: [],
        findFeature: '',
        foundFeatures: [],
        allMembers: [],
        allFeatures: [],
        defaults: {
          user: "",
          jclLib: "",
          db2Schema: ""
        },
        addMemberDialogVisible: false,
        addFeatureDialogVisible: false,
        addMemberForm: {
          email: "",
          role: "Developer"
        },
        addFeatureForm: {
          featureName: "F",
          featureDesc: ""
        },
        buttonLoading: false
      };
    },
    methods: {
      handleTabClick(tab, event) {
        // Handle Tab clicks
        switch (tab.index) {
          case '1':   // People
            this.getMembers();
            break;
          case '2':   // Defaults
            this.getDefaults();
            break;
          case '3':
            this.getFeatures();
            break;
        }
      },
      getEnv() {
        const promise = this.$axios({
          url: '/api/rest/getEnv',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace")
          }
        }).then((res) => {
          this.env.envDesc = res.data.result['ENV_DESP'].trim();
        });
      },
      saveEnv() {
        this.buttonLoading = true;
        const promise = this.$axios({
          url: '/api/rest/saveEnv',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace"),
            "envDesc": this.env.envDesc,
            "lastActUserId": parseInt(sessionStorage.getItem('userid'))
          }
        }).then((res) => {
          this.buttonLoading = false;
          this.$message({
            type: 'success',
            message: 'Saved!'
          });
        }, (err) => {
          this.buttonLoading = false;
          this.$message({
            type: 'error',
            message: 'Save failed. Please contact administrator.'
          });
        });
      },
      getMembers() {
        const promise = this.$axios({
          url: '/api/rest/getMembers',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace")
          }
        }).then((res) => {
          this.allMembers.splice(0, this.allMembers.length);
          this.foundMembers.splice(0, this.foundMembers.length);
          res.data.result.forEach(element => {
            let member = {
              name: element['USER_NAME'].trim(),
              email: element['USER_EMAIL'].trim(),
              role: element['USER_ROLE'].trim(),
              status: element['APPR_STATUS'].trim(),
              img: 'https://w3-services1.w3-969.ibm.com/myw3/unified-profile-photo/v1/image/' + element['USER_EMAIL'].trim()
            };
            this.allMembers.push(member);
            this.foundMembers.push(member);
          });
        });
      },

      getFeatures() {
        const promise = this.$axios({
          url: '/api/git/getFeatures',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace")
          }
        }).then((res) => {
          this.allFeatures.splice(0, this.allFeatures.length);
          this.foundFeatures.splice(0, this.foundFeatures.length);
          res.data.result.forEach(element => {
            let member = {
              featureName: element['FEATURE_NAME'].trim(),
              featureDesc: element['FEATURE_DESC'].trim(),
              status: element['APPR_STATUS'].trim()
            };
            this.allFeatures.push(member);
            this.foundFeatures.push(member);
          });
        });
      },
      showAddMemberDialog() {
        this.addMemberDialogVisible = true;
      },
      showAddFeatureDialog() {
        this.addFeatureDialogVisible = true;
      },
      addMember() {
        this.buttonLoading = true;
        this.addMemberForm.email = this.addMemberForm.email.trim();
        // search BluePages
        const promise = this.$axios({
          url: '/api/rest/searchBluePages',
          method: 'post',
          data: {
            "email": this.addMemberForm.email
          }
        }).then((res) => {
          if (res.data.result == "success") {
            // add member
            let userName = res.data.name;
            const promise = this.$axios({
              url: '/api/rest/addMember',
              method: 'post',
              data: {
                "envCode": sessionStorage.getItem("workspace"),
                "userName": userName,
                "userEmail": this.addMemberForm.email,
                "userRole": this.addMemberForm.role,
                "lastActUserId": parseInt(sessionStorage.getItem('userid'))
              }
            }).then((res) => {
              if (res.data.result == "success") {
                // push to allMembers
                let member = {
                  name: userName,
                  email: this.addMemberForm.email,
                  role: this.addMemberForm.role,
                  status: 'Approved',
                  img: 'https://w3-services1.w3-969.ibm.com/myw3/unified-profile-photo/v1/image/' + this.addMemberForm.email
                };
                this.allMembers.push(member);
                // push to foundMembers if found
                if (member.name.toUpperCase().indexOf(this.findMember.toUpperCase()) >= 0
                  || member.email.toUpperCase().indexOf(this.findMember.toUpperCase()) >= 0) {
                  this.foundMembers.push(member);
                }
                // reset and show message
                this.addMemberForm.email = "";
                this.buttonLoading = false;
                this.$message({
                  type: 'success',
                  message: 'Member Added.'
                });
              }
            }, (err) => {
              this.buttonLoading = false;
              this.$message({
                type: 'error',
                message: 'Add failed. Please contact administrator.'
              });
            });
          } else {
            this.buttonLoading = false;
            this.$message({
              type: 'error',
              message: 'Email address not found in BluePages.'
            });
          }
        }, (err) => {
          this.buttonLoading = false;
          this.$message({
            type: 'error',
            message: 'BluePages unavailable. Please contact administrator.'
          });
        });
      },
      addFeature() {
        this.buttonLoading = true;
        this.addFeatureForm.email = this.addFeatureForm.featureName.trim();
        // search BluePages
        const promise = this.$axios({
          url: '/api/git/addFeature',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace"),
            "featureName": this.addFeatureForm.featureName,
            "featureDesc": this.addFeatureForm.featureDesc,
            "lastActUserId": parseInt(sessionStorage.getItem('userid'))
          }
        }).then((res) => {
          if (res.data.result == "success") {
            // push to allMembers
            let member = {
              featureName: this.addFeatureForm.featureName,
              featureDesc: this.addFeatureForm.featureDesc,
              status: 'Approved'
            };
            this.allFeatures.push(member);
            // push to foundMembers if found
            if (member.featureName.toUpperCase().indexOf(this.findFeature.toUpperCase()) >= 0
              || member.featureDesc.toUpperCase().indexOf(this.findFeature.toUpperCase()) >= 0) {
              this.foundFeatures.push(member);
            }
            // reset and show message
            this.addFeatureForm.featureName = "";
            this.buttonLoading = false;
            this.$message({
              type: 'success',
              message: 'Feature Added.'
            });
            this.addFeatureDialogVisible=false;
          }
        }, (err) => {
          this.buttonLoading = false;
          this.$message({
            type: 'error',
            message: 'Add failed. Please contact administrator.'
          });
        });
      },
      handleChangeRole(command) {
        const promise = this.$axios({
          url: '/api/rest/updateMemberRole',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace"),
            "userEmail": command.email,
            "userRole": command.role,
            "lastActUserId": parseInt(sessionStorage.getItem('userid'))
          }
        }).then((res) => {
          if (res.data.result == "success") {
            this.allMembers.forEach(member => {
              if (member.email == command.email) {
                member.role = command.role;
              }
            });
          }
        });
      },
      handleChangeStatus(command) {
        const promise = this.$axios({
          url: '/api/rest/updateMemberStatus',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace"),
            "userEmail": command.email,
            "apprStatus": command.status,
            "lastActUserId": parseInt(sessionStorage.getItem('userid'))
          }
        }).then((res) => {
          if (res.data.result == "success") {
            this.allMembers.forEach(member => {
              if (member.email == command.email) {
                member.status = command.status;
              }
            });
          }
        });
      },
      handleChangeFeatureStatus(command) {
        const promise = this.$axios({
          url: '/api/git/updateFeatureStatus',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace"),
            "featureName": command.featureName,
            "apprStatus": command.status,
            "lastActUserId": parseInt(sessionStorage.getItem('userid'))
          }
        }).then((res) => {
          if (res.data.result == "success") {
            this.allFeatures.forEach(feature => {
              if (feature.featureName == command.featureName) {
                feature.status = command.status;
              }
            });
          }
        });
      },
      getDefaults() {
        const promise = this.$axios({
          url: '/api/rest/getDefaults',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace")
          }
        }).then((res) => {
          this.defaults.user = res.data.result['USER'].trim();
          this.defaults.jclLib = res.data.result['JCLLIB ORDER'].trim();
          this.defaults.db2Schema = res.data.result['DB2 Schema'].trim();
        });
      },
      saveDefaults() {
        this.buttonLoading = true;
        // prepare defaults array
        let defaults = [];
        defaults.push({
          "name": "USER",
          "value": this.defaults.user
        });
        defaults.push({
          "name": "JCLLIB ORDER",
          "value": this.defaults.jclLib
        });
        defaults.push({
          "name": "DB2 Schema",
          "value": this.defaults.db2Schema
        });
        // call ajax
        const promise = this.$axios({
          url: '/api/rest/saveEnvDefaults',
          method: 'post',
          data: {
            "envCode": sessionStorage.getItem("workspace"),
            "defaults": defaults,
            "lastActUserId": parseInt(sessionStorage.getItem('userid'))
          }
        }).then((res) => {
          this.buttonLoading = false;
          this.$message({
            type: 'success',
            message: 'Saved!'
          });
        }, (err) => {
          this.buttonLoading = false;
          this.$message({
            type: 'error',
            message: 'Save failed. Please contact administrator.'
          });
        });
      }
    },
    mounted() {
      let userRole = sessionStorage.getItem("userrole");
      if (userRole != "Admin") {
        this.$router.push("/");
      }
      this.getEnv();
    },
    watch: {
      findMember: function (val) {
        this.foundMembers.splice(0, this.foundMembers.length);
        this.allMembers.forEach(element => {
          if (element.name.toUpperCase().indexOf(val.toUpperCase()) >= 0
            || element.email.toUpperCase().indexOf(val.toUpperCase()) >= 0) {
            this.foundMembers.push(element);
          }
        });
      }
    },
    watch: {
      findFeature: function (val) {
        this.foundFeatures.splice(0, this.foundFeatures.length);
        this.allFeatures.forEach(element => {
          if (element.featureName.toUpperCase().indexOf(val.toUpperCase()) >= 0) {
            this.foundFeatures.push(element);
          }
        });
      }
    }

  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  a:link {
    color: blue;
  }

  a:visited {
    color: blue;
  }

  a:hover {
    color: blue;
    text-decoration: underline;
  }

  a:active {
    color: blue;
  }

  .settings-container {
    width: 1000px;
    margin-top: 20px;
    margin-right: auto;
    margin-left: auto;
  }

  .user-avator * {
    vertical-align: middle;
    font-size: 14px;
  }

  .user-avator img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
  }
</style>



